import React, { Component } from 'react';
import Pictureattachments from './Pictureattachments';
import ContentEditable from 'react-contenteditable';
import uuid from 'uuid/v4';

class Editorpanel extends Component {
	constructor(props) {
		super(props);

		this.contentEditable = React.createRef();

		this.handleChange = this.handleChange.bind(this);
		this.setNewDate = this.setNewDate.bind(this);

		this.state = { note: this.props.notetoedit };
	}

	componentWillMount() {
		this.setState({ note: this.props.notetoedit });
	}
	componentDidMount() {
		this.setState({ note: this.props.notetoedit });
	}

	setNewDate() {
		let d = new Date();
		let year = d.getFullYear();
		let month = d.getMonth() < 10 ? '0' + (d.getMonth() + 1) : d.getMonth() + 1;
		let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate();
		let fullDate = `${year}-${month}-${day}`;
		return fullDate;
	}
	handleChange(e) {
		this.setState({
			note: { body: e.target.value, title: e.target.value.substring(0, 20) }
		});
		this.props.handlechange(this.state.note);
	}

	render() {
		return (
			<div id="right-panel">
				<div id="drag" />
				<h3 className="title">{this.state.note.title}</h3>
				<Pictureattachments />

				<ContentEditable
					id="editor"
					className="editor"
					innerRef={this.contentEditable}
					html={this.state.note.body} // innerHTML of the editable div
					disabled={this.props.canedit} // use true to disable editing
					onChange={this.handleChange} // handle innerHTML change
					tagName="div" // Use a custom HTML tag (uses a div by default)
				/>
			</div>
		);
	}
}
export default Editorpanel;
