import React, { Component } from 'react';
import Locknote from './components/Locknote';
import Toolbar from './components/Toolbar';
import Notespanel from './components/Notespanel';
import Editorpanel from './components/Editorpanel';
import uuid from 'uuid/v4';

import Dragging from './components/Functions';
import './components/App.css';

class App extends Component {
	constructor(props) {
		super(props);
		this.handleLockClicked = this.handleLockClicked.bind(this);
		this.handleNoteSelected = this.handleNoteSelected.bind(this);
		this.handleAddNote = this.handleAddNote.bind(this);
		this.handleDeleteNote = this.handleDeleteNote.bind(this);
		this.handleUpdate = this.handleUpdate.bind(this);
		this.handleGetNote = this.handleGetNote.bind(this);

		this.setNewDate = this.setNewDate.bind(this);

		this.state = {
			notes: [
				{
					id: uuid(),
					title: 'Default title',
					body: 'This is a default note',
					date: this.setNewDate(),
					locked: false,
					haspictures: false,
					picturepath: './pics/'
				}
			],
			id: '',
			locked: false,
			selected: {}
		};
	}
	// -------------- Handlers

	setNewDate() {
		let d = new Date();
		let year = d.getFullYear();
		let month = d.getMonth() < 10 ? '0' + (d.getMonth() + 1) : d.getMonth() + 1;
		let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate();
		let fullDate = `${year}-${month}-${day}`;
		return fullDate;
	}
	//--------- Lifecycles ----

	//--------- Called before the render of all components
	componentWillMount() {
		this.setState({
			id: this.state.notes[0].id,
			locked: this.state.notes[0].locked,
			selected: this.state.notes[0]
		});
	}
	//-------- Called after rendering of all components
	componentDidMount() {
		Dragging();
	}

	// Enable or disable note edit
	handleLockClicked() {
		this.setState({ locked: (this.locked = !this.locked) });
	}

	// add note to state.selected on note selection
	handleNoteSelected(e) {
		let getIndex = this.state.notes.findIndex(el => {
			return el.id === this.state.id;
		});

		this.setState({
			id: e,
			selected: this.state.notes.filter(note => note.id === this.state.id)
		});
		console.log(this.state.selected);
	}
	// add a new note
	handleAddNote() {
		const newNote = {
			id: uuid(),
			title: 'added note',
			body: 'This note was added',
			date: this.setNewDate(),
			locked: false,
			haspictures: false,
			picturepath: './pics/'
		};

		this.setState({ notes: [...this.state.notes, newNote], selected: newNote });
	}

	handleDeleteNote() {
		console.log('delete pressed for id', this.state.id);
		this.setState({
			notes: [...this.state.notes.filter(note => note.id !== this.state.id)]
		});
	}

	//this should update both the state.note and the state.selected
	handleUpdate(e) {
		let getIndex = this.state.notes.findIndex(el => {
			return el.id === this.state.id;
		});

		let newNotesArray = [...this.state.notes];
		let noteToUpdate = { ...newNotesArray[getIndex] };

		noteToUpdate.body = e.body;
		newNotesArray[getIndex] = noteToUpdate;

		this.setState({ notes: newNotesArray, selected: noteToUpdate });

		// this shows that the state is updated
		console.log(this.state.notes);
	}

	handleGetNote() {
		let getIndex = this.state.notes.findIndex(el => {
			return el.id === this.state.id;
		});
		return this.state.selected;
	}

	render() {
		return (
			<div className="App" id="container">
				<Locknote
					editorlock={this.handleLockClicked}
					caneditnote={this.state.locked}
				/>

				<Toolbar
					addnote={this.handleAddNote}
					deletenote={this.handleDeleteNote}
				/>
				<Notespanel
					notes={this.state.notes}
					noteselected={this.handleNoteSelected}
					id={this.state.id}
				/>
				<Editorpanel
					handlechange={this.handleUpdate}
					canedit={this.state.locked}
					notetoedit={this.state.selected}
					body={this.state.selected.body}
					html={this.state.selected.body}
				/>
			</div>
		);
	}
}

export default App;
