import React, { Component } from 'react';

class Notespanel extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		const notes = this.props.notes.map(note => {
			return (
				<li
					key={note.id}
					onClick={this.props.noteselected.bind(this, note.id)}
					className={note.selected ? 'selected' : ''}>
					<h2>{note.title}</h2>
					<p>{note.date}</p>
				</li>
			);
		});
		return (
			<div id="left-panel">
				<ul>{notes}</ul>
			</div>
		);
	}
}
export default Notespanel;
